import React, { useState } from 'react';
import './Report.css';

const Report: React.FC = () => {
  const [location, setLocation] = useState('위치 정보를 가져올 수 없습니다.');

  const handleGetLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation(`위도: ${position.coords.latitude}, 경도: ${position.coords.longitude}`);
        },
        () => {
          setLocation('위치 정보를 가져올 수 없습니다.');
        }
      );
    } else {
      setLocation('브라우저가 위치 정보를 지원하지 않습니다.');
    }
  };

  return (
    <div className="report-container">
      <div className="report-header">
        <h1>차량 신고</h1>
      </div>

      <div className="report-section">
        <button onClick={handleGetLocation}>위치 가져오기</button>
        <div className="location-status">
          <strong>위치 정보:</strong> {location}
        </div>
      </div>

      <div className="report-section">
        <h2>사진 및 동영상 등록</h2>
        <input type="file" />
      </div>

      <div className="report-section">
        <h2>신고 내용</h2>
        <input type="text" placeholder="신고 내용을 입력해 주세요." />
      </div>

      <div className="report-submit">
        <button>신고하기</button>
      </div>
    </div>
  );
};

export default Report;
